(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('NavbarController', function (profileService, $mdSidenav, $log, navbarMenuList) {
      var vm = this;

      vm.toggleLeft = buildToggler('left');
      vm.menuSections = navbarMenuList;

      profileService.getProfile().then(function(user) {
        vm.user = user;
      });
     
      function buildToggler(navID) {
        return function() {
          $mdSidenav(navID)
            .toggle()
            .then(function () {
              $log.debug("toggle " + navID + " is done");
            });
        }
      }
    })

})();
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .service('authorisationService', authorisationService)
    .service('authorisationServiceMock', authorisationServiceMock)
    .config(function($provide){
      $provide.decorator('authorisationService', function($delegate, authorisationServiceMock) {
        $delegate = authorisationServiceMock;
        return $delegate;
      });
    });

  /** @ngInject */
  function authorisationService($http) {
    this.login = function() {
      return $http.get('api/login');
    };

    this.logout = function() {
      return $http.get('api/logout');
    };
  }

  /** @ngInject */
  function authorisationServiceMock($http) {
    this.login = function() {
        return $http.get('app/components/authorisation/mock/login.mock.json');  
    };

    this.logout = function() {
      return $http.get('app/components/authorisation/mock/logout.mock.json');
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('ProfileController', ProfileController);

  /** @ngInject */
  function ProfileController(profileService, $log){
    var vm = this;

    vm.genders = {
      M: 'Male',
      F: 'Female'
    };

    profileService.getProfile().then(function(user) {
      vm.user = user;
    });

    vm.submitForm = function() {
      // check to make sure the form is completely valid
      if (vm.userForm.$valid) {
        alert('our form is amazing');
      }

    };
    vm.save = function() {
      $log.info("save");
      vm.message = true; //временно
      profileService.saveProfile(vm.user).then(function(){
        /*vm.message = true;*/
      });
    }
  }
})();
(function() {
  'use strict';


angular
  .module('gpSocial')
  .directive('checkLastNameDirective', checkLastNameDirective);

  function checkLastNameDirective(){
    
    return {
      require: 'ngModel',
      link: function (scope, element, attributes, ctrl) {
          ctrl.$validators.checkLastNameDirective = function (modelValue) {
              scope.lastNameError = true;
              if (ctrl.$isEmpty(modelValue)) // if empty, correct value
              {   
                  scope.lastNameError = false;
                  scope.lastNameErrorText = 'Last Name should exist';
                  return scope.lastNameError;
              }
              if (modelValue.length >= 3 && modelValue.length <= 20){ // correct value
                  scope.lastNameErrorText = 'Last Name should contain at least 3 letters';
                  scope.lastNameError = true;
                  return scope.lastNameError;
              }else{
                scope.lastNameError = false;
                return scope.lastNameError;
              }
          };
      }
    };
  }




})();
